/* =================================================================== */
/* Animation Components - NWFTH Design System                       */
/* Performance-optimized animations for warehouse applications      */
/* =================================================================== */

/* =================================================================== */
/* Background Animation Classes - Based on Mobile-Rust              */
/* =================================================================== */

/* Gradient Morphing Background */
.animate-gradient-morph {
  background: linear-gradient(-45deg, #523325, #6B4423, #523325, #7A4D28);
  background-size: 400% 400%;
  animation: gradientMorph 8s ease-in-out infinite;
  will-change: background-position;
}

/* Multi-layer Particle System */
.animate-particles-layer-1::before,
.animate-particles-layer-2::before,
.animate-particles-layer-3::before {
  content: '';
  position: absolute;
  background: rgba(240, 180, 41, 0.3);
  border-radius: 50%;
  will-change: transform, opacity;
}

.animate-particles-layer-1::before {
  width: 4px;
  height: 4px;
  animation: particlesLayer1 15s linear infinite;
  left: 10%;
  top: 100%;
}

.animate-particles-layer-2::before {
  width: 6px;
  height: 6px;
  background: rgba(240, 180, 41, 0.2);
  animation: particlesLayer2 20s linear infinite;
  left: 80%;
  top: 100%;
}

.animate-particles-layer-3::before {
  width: 3px;
  height: 3px;
  background: rgba(240, 180, 41, 0.4);
  animation: particlesLayer3 25s linear infinite;
  left: 50%;
  top: 100%;
}

/* Floating Orbs */
.animate-floating-orbs::before {
  content: '';
  position: absolute;
  width: 80px;
  height: 80px;
  background: radial-gradient(circle, rgba(240, 180, 41, 0.08) 0%, transparent 60%);
  border-radius: 50%;
  animation: floatingOrbs 12s ease-in-out infinite;
  left: 20%;
  top: 30%;
  will-change: transform, opacity;
}

/* =================================================================== */
/* Keyframe Definitions                                              */
/* =================================================================== */

@keyframes gradientMorph {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes particlesLayer1 {
  0% {
    transform: translateY(100vh) translateX(-10px);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh) translateX(10px);
    opacity: 0;
  }
}

@keyframes particlesLayer2 {
  0% {
    transform: translateY(100vh) translateX(10px);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh) translateX(-10px);
    opacity: 0;
  }
}

@keyframes particlesLayer3 {
  0% {
    transform: translateY(100vh) translateX(-5px);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh) translateX(5px);
    opacity: 0;
  }
}

@keyframes floatingOrbs {
  0%, 100% {
    transform: translateY(0px) translateX(0px);
    opacity: 0.1;
  }
  33% {
    transform: translateY(-20px) translateX(10px);
    opacity: 0.3;
  }
  66% {
    transform: translateY(10px) translateX(-10px);
    opacity: 0.2;
  }
}

/* =================================================================== */
/* UI Component Animations                                           */
/* =================================================================== */

/* Fade In/Out */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.animate-fade-in {
  animation: fadeIn var(--animation-duration-normal) var(--animation-easing);
}

.animate-fade-out {
  animation: fadeOut var(--animation-duration-normal) var(--animation-easing);
}

/* Slide Animations */
@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-slide-in-up {
  animation: slideInUp var(--animation-duration-normal) var(--animation-easing);
}

.animate-slide-in-down {
  animation: slideInDown var(--animation-duration-normal) var(--animation-easing);
}

/* Scale Animations */
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes scaleOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.9);
  }
}

.animate-scale-in {
  animation: scaleIn var(--animation-duration-fast) var(--animation-easing);
}

.animate-scale-out {
  animation: scaleOut var(--animation-duration-fast) var(--animation-easing);
}

/* Bounce Animation */
@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translate3d(0,0,0);
  }
  40%, 43% {
    transform: translate3d(0, -30px, 0);
  }
  70% {
    transform: translate3d(0, -15px, 0);
  }
  90% {
    transform: translate3d(0, -4px, 0);
  }
}

.animate-bounce {
  animation: bounce 1s ease-in-out infinite;
}

/* Pulse Animation */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Shake Animation - For error states */
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-5px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(5px);
  }
}

.animate-shake {
  animation: shake 0.5s ease-in-out;
}

/* =================================================================== */
/* Loading Animations                                                */
/* =================================================================== */

/* Spinner */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

/* Dots Loading */
@keyframes dotPulse {
  0%, 80%, 100% {
    transform: scale(0);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-dots::before,
.animate-dots::after {
  content: '';
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: currentColor;
  animation: dotPulse 1.5s infinite ease-in-out;
}

.animate-dots::before {
  animation-delay: -0.32s;
  margin-right: 0.25rem;
}

.animate-dots::after {
  animation-delay: -0.16s;
  margin-left: 0.25rem;
}

/* =================================================================== */
/* Hover Effects                                                     */
/* =================================================================== */

.hover-lift:hover {
  transform: translateY(-2px);
  transition: transform var(--animation-duration-fast) var(--animation-easing);
}

.hover-grow:hover {
  transform: scale(1.05);
  transition: transform var(--animation-duration-fast) var(--animation-easing);
}

.hover-glow:hover {
  box-shadow: 0 0 20px rgba(240, 180, 41, 0.3);
  transition: box-shadow var(--animation-duration-normal) var(--animation-easing);
}

/* =================================================================== */
/* Accessibility & Performance                                       */
/* =================================================================== */

/* Respect user preferences */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }

  .animate-gradient-morph,
  .animate-particles-layer-1,
  .animate-particles-layer-2,
  .animate-particles-layer-3,
  .animate-floating-orbs {
    animation: none;
  }

  .hover-lift:hover,
  .hover-grow:hover {
    transform: none;
  }
}

/* Performance optimizations */
.animate-gpu {
  transform: translateZ(0);
  will-change: transform;
}

/* Delay utilities */
.animation-delay-1 { animation-delay: 0.1s; }
.animation-delay-2 { animation-delay: 0.2s; }
.animation-delay-3 { animation-delay: 0.3s; }
.animation-delay-4 { animation-delay: 0.4s; }
.animation-delay-5 { animation-delay: 0.5s; }