/* =================================================================== */
/* PK (Partial Picking System) - Modern CSS Architecture              */
/* Angular 20 + Standard CSS with NWFTH Brown Theme                  */
/* Based on Mobile-Rust proven patterns and Angular best practices   */
/* =================================================================== */

/* CSS Custom Properties - NWFTH Brand Theme */
:root {
  /* NWFTH Brand Colors */
  --color-brand-brown: #523325;
  --color-brand-amber: #F0B429;
  --color-brand-cream: #F5F5DC;

  /* HSL Color System for better CSS manipulation */
  --color-brand-brown-hsl: 20 50% 25%;
  --color-brand-amber-hsl: 43 87% 56%;
  --color-brand-cream-hsl: 60 56% 91%;

  /* Semantic Color System */
  --color-background: 0 0% 100%;
  --color-foreground: 25 65% 15%;
  --color-muted: 60 30% 97%;
  --color-muted-foreground: 25 40% 35%;
  --color-border: 25 20% 85%;
  --color-accent: var(--color-brand-amber-hsl);
  --color-primary: var(--color-brand-brown-hsl);

  /* Design System Variables */
  --radius: 0.5rem;
  --shadow-soft: 0 2px 8px rgba(60, 36, 21, 0.08);
  --shadow-medium: 0 4px 12px rgba(60, 36, 21, 0.12);
  --shadow-large: 0 8px 24px rgba(60, 36, 21, 0.15);

  /* Animation Variables */
  --animation-duration-fast: 0.15s;
  --animation-duration-normal: 0.3s;
  --animation-duration-slow: 0.5s;
  --animation-easing: cubic-bezier(0.4, 0, 0.2, 1);
}

/* =================================================================== */
/* Base Styles - Modern CSS Reset                                     */
/* =================================================================== */

*,
*::before,
*::after {
  box-sizing: border-box;
  border-color: hsl(var(--color-border));
}

html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
  font-feature-settings: "rlig" 1, "calt" 1;
}

body {
  margin: 0;
  font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI',
               'Roboto', sans-serif;
  background-color: hsl(var(--color-background));
  color: hsl(var(--color-foreground));
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Enhanced Focus Styles for Accessibility */
:focus-visible {
  outline: 2px solid hsl(var(--color-brand-amber-hsl));
  outline-offset: 2px;
}

/* Form Elements Inheritance */
input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
}

/* Button Reset */
button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

/* Link Reset */
a {
  color: inherit;
  text-decoration: none;
}

/* =================================================================== */
/* Utility Classes - Essential Layout & Spacing                       */
/* =================================================================== */

.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

.sr-only:focus,
.sr-only:active {
  position: static !important;
  width: auto !important;
  height: auto !important;
  padding: 0.5rem 1rem !important;
  margin: 0 !important;
  overflow: visible !important;
  clip: auto !important;
  white-space: normal !important;
  background: hsl(var(--color-background)) !important;
  color: hsl(var(--color-foreground)) !important;
  border: 2px solid hsl(var(--color-accent)) !important;
  border-radius: var(--radius) !important;
  z-index: 9999 !important;
}

/* =================================================================== */
/* Essential Flexbox & Grid Utilities                                 */
/* =================================================================== */

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.min-h-screen {
  min-height: 100vh;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

.inset-0 {
  inset: 0;
}

/* =================================================================== */
/* Spacing System                                                     */
/* =================================================================== */

.p-4 { padding: 1rem; }
.p-6 { padding: 1.5rem; }
.p-8 { padding: 2rem; }

.px-3 { padding-left: 0.75rem; padding-right: 0.75rem; }
.px-4 { padding-left: 1rem; padding-right: 1rem; }
.px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }

.py-2 { padding-top: 0.5rem; padding-bottom: 0.5rem; }
.py-3 { padding-top: 0.75rem; padding-bottom: 0.75rem; }

.space-y-2 > * + * { margin-top: 0.5rem; }
.space-y-4 > * + * { margin-top: 1rem; }
.space-y-6 > * + * { margin-top: 1.5rem; }

.gap-2 { gap: 0.5rem; }
.gap-3 { gap: 0.75rem; }
.gap-4 { gap: 1rem; }

/* =================================================================== */
/* Responsive Breakpoints & Text                                      */
/* =================================================================== */

.text-xs { font-size: 0.75rem; line-height: 1rem; }
.text-sm { font-size: 0.875rem; line-height: 1.25rem; }
.text-base { font-size: 1rem; line-height: 1.5rem; }
.text-lg { font-size: 1.125rem; line-height: 1.75rem; }
.text-xl { font-size: 1.25rem; line-height: 1.75rem; }
.text-2xl { font-size: 1.5rem; line-height: 2rem; }

.font-medium { font-weight: 500; }
.font-semibold { font-weight: 600; }
.font-bold { font-weight: 700; }

.text-center { text-align: center; }

/* =================================================================== */
/* Color Utilities                                                    */
/* =================================================================== */

.text-foreground {
  color: hsl(var(--color-foreground));
}

.text-muted-foreground {
  color: hsl(var(--color-muted-foreground));
}

.bg-background {
  background-color: hsl(var(--color-background));
}

.bg-muted {
  background-color: hsl(var(--color-muted));
}

.border {
  border-width: 1px;
}

.border-border {
  border-color: hsl(var(--color-border));
}

.rounded {
  border-radius: var(--radius);
}

.rounded-lg {
  border-radius: 0.5rem;
}

.rounded-full {
  border-radius: 9999px;
}

/* =================================================================== */
/* Transitions                                                        */
/* =================================================================== */

.transition-all {
  transition: all var(--animation-duration-fast) var(--animation-easing);
}

.transition-colors {
  transition: color var(--animation-duration-fast) var(--animation-easing),
              background-color var(--animation-duration-fast) var(--animation-easing),
              border-color var(--animation-duration-fast) var(--animation-easing);
}

/* =================================================================== */
/* Z-Index System                                                     */
/* =================================================================== */

.z-10 { z-index: 10; }
.z-20 { z-index: 20; }
.z-30 { z-index: 30; }
.z-50 { z-index: 50; }

/* =================================================================== */
/* Responsive Design - Warehouse Display Optimization                 */
/* =================================================================== */

/* Mobile Warning - Hidden on desktop */
@media (min-width: 1024px) {
  .mobile-warning {
    display: none !important;
  }
}

/* Mobile/Tablet - Emergency access only */
@media (max-width: 1023px) {
  .mobile-warning {
    display: block;
    margin-bottom: 1rem;
    padding: 0.75rem;
    text-align: center;
    font-size: 0.875rem;
    border-radius: var(--radius);
    background-color: var(--color-brand-amber);
    color: var(--color-brand-brown);
  }
}

/* =================================================================== */
/* Import Component Styles                                            */
/* =================================================================== */

@import './themes/nwfth-theme.css';
@import './components/cards.css';
@import './components/buttons.css';
@import './components/inputs.css';
@import './components/animations.css';